.MindMap-viewport {
  position: relative;
  width: 100vw;
  height: 100vh;
  background-color: #282c34;
  overflow: hidden;
}

.MindMap-content {
  position: absolute;
  width: 1000%;
  overflow: visible;
  font-size: 1cm;
  color: white;
}

.MindMap-root {
  position: absolute;
  border: solid 2px white;
  border-radius: 2em / 1em;
  padding: 0.5em;
  text-align: center;
}

.MindMap-root.selected {
  background-color: dodgerblue;
}

.MindMap-item {
  position: absolute;
  cursor: default;
}

.MindMap-item.selected {
  background-color: dodgerblue;
}

.MindMap-input {
  position: absolute;
  left: 0px;
  top: 0px;
  cursor: text;
  opacity: 0;
}

.MindMap-input.active {
  opacity: 1;
}

.MindMap-indicator {
  position: absolute;
  fill: none;
  stroke: orange;
  stroke-width: 2px;
}

.MindMap-manipulator {
  position: absolute;
}

.MindMap-collapsed {
  cursor: pointer;
}

.MindMap-collapsed svg {
  fill: transparent;
  stroke: white;
  stroke-width: 2px;
}

.MindMap-collapsed svg:hover {
  stroke: orange;
}

.MindMap-root:hover, .MindMap-item:hover {
  border-color: orange;
  color: orange;
}

.MindMap-content svg {
  position: absolute;
  width: 1024px;
  height: 1024px;
  transform: scale(0.0009765625);
  transform-origin: left top;
  overflow: visible;
}

.MindMap-content .line {
  fill: none;
  stroke: white;
  stroke-width: 2px;
}

.Menu {
  position: absolute;
  left: 0px;
  top: 0px;
  display: flex;
  font-size: 1cm;
  color: white;
}

.Menu div {
  cursor: pointer;
}

.Menu div:hover {
  color: orange;
}

.Menu div.disabled {
  color: gray;
}
